.rs-link {
  position: relative;
}

.rs-tooltip {
  display: none;
  position: absolute;
  background: white;
  border-radius: 5px;
  border: 1px solid #016600;
  padding: 7px;
  font-size: 14px;
  max-width: 200px;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 10px;
}

.rs-tooltip.tooltip-below {
  bottom: auto;
  top: 100%;
  margin-bottom: 0px;
  margin-top: 10px;
}
